<template>
  <div class="box">
    <index-header></index-header>
    <div class="wrapbox">
      <index-focus-max></index-focus-max>
      <index-menu></index-menu>
      <index-focus-min></index-focus-min>
      <index-section :data="indexproduct"></index-section>
    </div>
    <index-footer></index-footer>
  </div>
</template>

<script>
  // import indexcss from '../assets/index.css';
  import indexProduct from "../apis/indexProduct";
  import IndexHeader from "../components/index/IndexHeader";
  import IndexSection from "../components/index/IndexSection";
  import IndexFooter from "../components/index/IndexFooter";
  import IndexMenu from '../components/index/IndexMenu'
  import IndexFocusMin from '../components/index/IndexFocusMin'
  import IndexFocusMax from '../components/index/IndexFocusMax'
  export default {
    name: "index",
    components: {IndexFocusMax, IndexFocusMin, IndexMenu, IndexFooter, IndexSection, IndexHeader},
    methods :{
      _initgetIndexProductData(){
        indexProduct.getindexProductData(data=>{// get..随便起
          this.indexproduct=data
          console.log(this.indexproduct);
        })
        }
      },
    created(){
      this._initgetIndexProductData()
    },
    data(){
      return {
        indexproduct:[]
      }
    }
  }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
    .wrapbox{
      flex: 1;
      overflow-x: hidden;
      overflow-y:auto;
    }
</style>



