<template>
  <div class="focus">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="i in swiperImgs"><img :src="i.swiperImg"/></div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
  import Swiper from "../../../bower_components/swiper/dist/js/swiper.js";
  export default {
    name: 'IndexFocusMax',
    data(){
      return {
        swiperImgs: [
          {swiperImg:"../../../static/images/1-1.jpg"},
          {swiperImg:"../../../static/images/1-2.jpg"},
          {swiperImg:"../../../static/images/1-3.jpg"},
          {swiperImg:"../../../static/images/1-4.jpg"},
          {swiperImg:"../../../static/images/1-5.jpg"},
          {swiperImg:"../../../static/images/1-6.jpg"},
          {swiperImg:"../../../static/images/1-7.jpg"},
          {swiperImg:"../../../static/images/1-8.jpg"},
          {swiperImg:"../../../static/images/1-9.jpg"},
          {swiperImg:"../../../static/images/1-10.jpg"}
          ]
      }
    },
    mounted () {
      var swiper = new Swiper('.swiper-container', {
        pagination: {
          el: '.swiper-pagination',
        },
        autoplay:true,
        loop:true
      });
    }
  }
</script>

<style scoped>
    @import "../../../bower_components/swiper/dist/css/swiper.css";
    @import  "../../assets/index.css";

</style>



