  <div class="box">
    <introduce-header></introduce-header>
    <introduce-section></introduce-section>
    <introduce-footer></introduce-footer>
  </div>
</template>

<script>
  import IntroduceHeader from '../components/introduce/IntroduceHeader'
  import IntroduceSection from '../components/introduce/IntroduceSection'
  import IntroduceFooter from '../components/introduce/IntroduceFooter'
  export default {
    name: 'introduce',
    components: {IntroduceFooter, IntroduceSection, IntroduceHeader}
  }
</script>

<style scoped>

  .box{
    display: flex;
    flex-direction: column;
  }
</style>



