<template>
  <div class="wrapper">
    <div class="flexbox">
      <product-detail-focus :productdetailfocus="productDetail"></product-detail-focus>
      <product-detail-content :prodetcon="productDetail"></product-detail-content>
      <product-like :prolike="productDetail"></product-like>
    </div>
    <product-detail-footer></product-detail-footer>

  </div>
</template>

<script>
  import productDetailcss from '../assets/productDetail.css';
  import productDetail from '../apis/productDetail'
  import ProductDetailContent from '../components/productDetail/ProductDetailContent'
  import ProductDetailFocus from '../components/productDetail/ProductDetailFocus'
  import ProductLike from '../components/productDetail/ProductLike'
  import ProductDetailFooter from '../components/productDetail/ProductDetailFooter'
  export default {
    name: 'productDetail',
    components: {ProductDetailFooter, ProductLike, ProductDetailFocus, ProductDetailContent,productDetailcss},
    methods :{
      _initgetproductDetailData(){
        productDetail.getproductDetailData(data=>{
          this.productDetail=data
          console.log(this.productDetail);
        })
      }
    },
    created(){
      this._initgetproductDetailData();
    },
    data(){
      return {
        productDetail:[]
      }
    }
  }
</script>

<style scoped>
  .wrapper{
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
  }
  .flexbox{
    flex: 1;
    overflow-y: auto;
  }
</style>



