<template>
  <div id="box">
    <div class="footer">
      <div class="litmenuBox">
        <div class="litmenu">
          <img src="../../../static/images/pd1.jpg" alt="">
          <span>更多</span>
        </div>
        <div class="litmenu">
          <img src="../../../static/images/pd2.jpg" alt="">
          <span>已收藏</span>
        </div>
        <div class="litmenu">
          <img src="../../../static/images/pd3.jpg" alt="">
          <span>客服</span>
        </div>
      </div>
      <div class="buy buy1">
        <h3>￥155</h3>
        <h3>单独购买</h3>
      </div>
      <div class="buy buy2" v-on:click="toggle()">
        <h3>￥149.9</h3>
        <h3>发起拼单</h3>
      </div>
    </div>
    <!--<div class="opacityBox">-->
      <div class="paybox" v-model="isShow" v-show="isShow">
        <div class="pay">
          <div class="selectPrice">
            <div class="clothImgBox">
              <img class="clothImg" src="../../../static/images/payImg.jpg" alt="">
            </div>
            <div class="payMoney">
              <h3>￥<input v-for="(n,index) in payPrice" v-show="index==num" v-model="payPrice[index]" v-bind:placeholder="placeholderValue"></h3>
              <h4>请选择<span class="colorSpan">颜色</span><span>尺码</span></h4>
            </div>
            <div class="payClose" v-on:click="toggle()">
              <img src="../../../static/images/payClose.jpg" alt="">
            </div>
            <div class="payColor">
              <h3>颜色</h3>
              <span :key="index" v-for="(i,index) in payColor" class="selectColor" :class="{ tored:changeRed == index}" @click="change(index)">{{i}}</span>
            </div>
            <div class="size">
              <h3>尺码</h3>
              <span :key="index" v-for="(j,index) in paySize" class="selectSize" :class="{ tosize:changeSize == index}" @click="change2(index)">{{j.selsize}}</span>
            </div>
            <div class="payNum">
              <span>数量</span>
              <div class="payNums">
                <img v-on:click="subtract()" src="../../../static/images/payjian.jpg" alt="">
                <input type="text" class="quality" v-model="quality"/>
                <img v-on:click="add()" src="../../../static/images/payjia.jpg" alt="">
              </div>
            </div>
            <div class="confir">
              <span>确定</span>
            </div>
          </div>
        </div>
      </div>
    <!--</div>-->

  </div>
</template>

<script>
  // import $ from '../../../static/js/jquery1.42.min';
  export default {
    name: 'ProductDetailFooter',
    // var vm = new Vue({
    //   el:'#box',
      data(){
        return {
          isShow:false,
          num:0,
          quality:1,
          placeholderValue:"49.9-199",
          payColor:[
            "单毛衣",
            "单裤子",
            "毛衣加裤子套装",
          ],
          payPrice:[
            "49.9","69.9","119"
          ],
          paySize:[
            { selsize:"S" },
            { selsize:"M" },
            { selsize:"L" },
            { selsize:"XL" }
          ],
          changeRed:-1,
          changeSize:-1
        }
      },
      methods:{
        toggle:function(){
          this.isShow = !this.isShow;
          console.log(this.isShow)
        },
        subtract:function () {
          this.quality--;
        },
        add:function () {
          this.quality++;
        },
        change(index){
          this.changeRed = index;
          this.num=index;
        },
        change2(index){
          this.changeSize = index;
        }
      }
    // })
  }
</script>
<style scoped>
  @import  "../../assets/productDetail.css";
  .payColor span{
    margin: 0 .05rem;
  }
  .tored{
     color: #fff;
     background-color: #e02e24;
   }
  .tosize{
    color: #fff;
    background-color: #e02e24;
  }
  .selectSize{
    margin: 0 .1rem;
  }
  .payColor{
    height: 1.2rem;
  }
  .size span{
    float: left;
    margin: .3rem 0.9em;
  }
  .payMoney input{
    color: #e02e24;
  }
</style>




