<template>
    <div class="box">
      <div class="pinNum">{{prodetpininfo.pinNum}}人在拼单</div>
      <div class="pinPer" :key="index"  v-for="(n,index) in prodetpininfo.pinSum">
        <div class="pinLeft">
          <img class="p1" :src="n.pinPersonImg" alt="">
          <span>{{n.pinPersonName}}</span>
        </div>
        <div class="pinRight">
          <div class="pinRightLeft">
            <h3>还差<span class="onePer">{{n.pinSuccessNum}}</span>人拼成</h3>
            <h4>{{n.pinSuccessTime}}</h4>
          </div>
          <div><span class="pinRightRight">去拼单</span></div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'ProductDetailContentPinInfo',
    props:["prodetpininfo"]
  }
</script>

<style scoped>
  @import  "../../assets/productDetail.css";
</style>



