<template>
  <div class="focus">
    <router-link to="/introduce">
      <img class="fpic fpic1" src="../../../static/images/zjt.jpg" alt="">
    </router-link>
    <img class="fpic fpic2" src="../../../static/images/fx.jpg" alt="">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="index" v-for="(n,index) in productdetailfocus.productDetailFocus">
          <img :id="n.proId" :src="n.SwiperImg"/>
        <!--<div class="swiper-slide" :key="index" v-for="(n,index) in productdetailfocus.productDetailFocus.item">-->
          <!--<img :src="item.SwiperImg"/>-->
        <!--</div>-->
      </div>
  </div>
  </div>
  </div>
</template>

<script>
  import Swiper from "../../../bower_components/swiper/dist/js/swiper.js";
  export default {
    name: 'ProductDetailFocus',
    props:["productdetailfocus"],
    data () {
      return {
        productDetailFocus: [
          { proId:'#num1',"SwiperImg": "../../../static/images/xq1.jpg" },
          { proId:'#num2',"SwiperImg": "../../../static/images/xq2.jpg" },
          { proId:'#num3',"SwiperImg": "../../../static/images/xq3.jpg" },
          { proId:'#num4',"SwiperImg": "../../../static/images/xq4.jpg" },
          { proId:'#num5',"SwiperImg": "../../../static/images/xq5.jpg" }
        ]
        // ,
        // item:{}
      }
    },
    mounted () {
      // console.log(this.$route.query.id);
      // this.item=this.productDetailFocus[this.$route.query.id];


      // var swiper = new Swiper('.swiper-container', {
      //   pagination: {
      //     el: '.swiper-pagination',
      //   },
      //   autoplay:true,
      //   loop:true
      // });
      //轮播图不播放，swiper提前初始化了，那个时候还没有数据，当数据调出来时已经没用了
      new Swiper('.swiper-container', {
        initialSlide :0,
        pagination: '.swiper-pagination',
        loop: true,
        speed: 400,
        autoplay: 2000,
        autoplayDisableOnInteraction: false,
        observer:true,//修改swiper自己或子元素时，自动初始化swiper
        observeParents:true//修改swiper的父元素时，自动初始化swiper
      })
    }
  }
</script>


<style scoped>
  @import  "../../assets/productDetail.css";
  @import "../../../bower_components/swiper/dist/css/swiper.css";
</style>



