import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import GoodLove from '@/pages/goodLove'
import search from '@/pages/search'
import introduce from '@/pages/introduce'
import productDetail from '@/pages/productDetail'
import myCenter from '@/pages/myCenter'
// import order from '@/pages/order'
Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/goodLove',
      name: 'goodLove',
      component: GoodLove
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/introduce',
      name: 'introduce',
      component: introduce
    }
    ,
    {
      path: '/productDetail',
      name: 'productDetail',
      component: productDetail
    }
    // ,
    // {
    //   path: '/order',
    //   name: 'order',
    //   component: order
    // }
    ,
    {
      path: '/myCenter',
      name: 'myCenter',
      component: myCenter
    }
  ]
})


