<template>
  <div class="box">
    <mycenter-nav></mycenter-nav>
    <div class="flex">
      <mycenter-header></mycenter-header>
      <mycenter-section :centersection="myCenter"></mycenter-section>
      <mycenter-footer></mycenter-footer>
    </div>
    <mycenter-bottom-menu></mycenter-bottom-menu>
  </div>
</template>

<script>
  import myCenter from '../apis/myCenter'   //导入
  import MycenterHeader from '../components/myCenter/MycenterHeader'
  import MycenterSection from '../components/myCenter/MycenterSection'
  import MycenterFooter from '../components/myCenter/MycenterFooter'
  import MycenterNav from '../components/myCenter/MycenterNav'
  import MycenterBottomMenu from '../components/myCenter/MycenterBottomMenu'
  export default {
    name: 'myCenter',
    components: {MycenterBottomMenu, MycenterNav, MycenterFooter, MycenterSection, MycenterHeader},
    methods :{
      _initgetmyCenterData(){
        myCenter.getmyCenterData(data=>{// get..随便起
          this.myCenter=data
          console.log(this.myCenter);
        })
      }
    },
    created(){
      this._initgetmyCenterData();
    },
    data(){
      return {
        myCenter:[]
      }
    }
  }
</script>

<style scoped>
  .box{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .flex{
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



