<template>
  <div class="footer">
    <ul class="footer">
      <li class="select" v-for="n in sels">
        <router-link :to="n.linkTo">
          <div class="sbox">
            <img :src="n.selimg"/>
            <span>{{n.selname}}</span>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'IntroduceFooter',
    data () {
      return {
        sels: [
          { linkTo:'/',selimg: '../../../static/images/sy.jpg', selname: '首页'},
          { linkTo:'/introduce',selimg: '../../../static/images/tjon.jpg', selname: '推荐'},
          { linkTo:'/search',selimg: '../../../static/images/ss.jpg', selname: '搜索'},
          { linkTo:'/search',selimg: '../../../static/images/lt.jpg', selname: '聊天'},
          { linkTo:'/myCenter',selimg: '../../../static/images/grzx.jpg', selname: '个人中心'}
        ]
      }
    }
  }
</script>

<style scoped>
  @import  "../../assets/introduce.css";
  .footer{
    height: .67rem;
  }
  a{
    color:#636563;
  }
</style>



