<template>
  <!--<div class="wrapper">-->
    <div class="productList">
      <div class="pageTit">
        <img src="../../../static/images/jxtj.jpg" alt="">
        <span>相似商品</span>
      </div>
      <div class="productBox">
        <ul>
          <router-link to="/productDetail">
            <li v-for="n in productInfo">
              <img class="proImg" :src="n.clothImg" alt="">
              <h3 class="proTit">{{proSort}}</h3>
              <div class="loveBox">
                <span class="love">极速退款</span>
                <span class="dress">时尚穿搭</span>
              </div>
              <div class="priceBox">
                <div class="moneySign">￥</div>
                <span class="proPrice">{{n.proPrice}}</span>
                <span class="pin">已拼<span class="num">{{n.num}}</span>件</span>
                <img class="pinPar" src="../../../static/images/find.jpg" alt="">
              </div>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
  <!--</div>-->
</template>

<script>
  export default {
    name: 'ProductLike',
    data () {
      return {
        productInfo: [
          {clothImg: '../../../static/images/tj1.jpg', proSort: '冬热卖',proPrice:'456.9',num:'234'},
          {clothImg: '../../../static/images/o01.jpg', proSort: '针织毛衣',proPrice:'87.9',num:'35'},
          {clothImg: '../../../static/images/tj2.jpg', proSort: '大衣',proPrice:'346.9',num:'264'},
          {clothImg: '../../../static/images/tj4.jpg', proSort: '羽绒服',proPrice:'56.9',num:'734'}
        ]
      }
    }
  }
</script>

<style scoped>
  @import  "../../assets/introduce.css";
</style>



