<template>
  <div class="footer">
    <ul class="footer">
      <li class="select" v-for="n in sels">
        <router-link :to="n.linkTo">
          <div class="sbox">
            <img :src="n.selimg"/>
            <span>{{n.selname}}</span>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'MycenterBottomMenu',
    data () {
      return {
        sels: [
          { linkTo:'/',selimg: '../../../static/images/sy.jpg', selname: '首页'},
          { linkTo:'/introduce',selimg: '../../../static/images/tj.jpg', selname: '推荐'},
          { linkTo:'/search',selimg: '../../../static/images/ss.jpg', selname: '搜索'},
          { linkTo:'/search',selimg: '../../../static/images/lt.jpg', selname: '聊天'},
          { linkTo:'/myCenter',selimg: '../../../static/images/myCenterOn.jpg', selname: '个人中心'}
        ]
      }
    }
  }
</script>

<style scoped>
  @import  "../../assets/myCenter.css";
  .footer{
    height: .67rem;
  }
  a{
    color:#636563;
  }
  /* footer */
  .footer{
    width: 100%;
    height: 0.47rem;
    font-size:.09rem;
    color:#636563;
    background-color: #fff;
    border-top:1px solid #e0e0e0;
    display: flex;
    justify-content: space-around;
  }
  .footer .sbox{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer img{
    width: 0.25rem;
    height: 0.24rem;
  }
  .footer .select{
    margin-top:.03rem;
  }
  .footer .on{
    color:#de2f21;
  }
</style>



