<template>
  <div class="box">
    <div class="header1">
      <router-link to="/">
      <div class="headerLeft">
        <img src="../../../static/images/s1img.jpg" alt="">
          <span>商品收藏</span>
      </div>
      </router-link>
      <div class="headerRight">
        <img src="../../../static/images/s2img.jpg" alt="">
      </div>
    </div>
    <div class="search1">
      <div class="seacon1">
        <img src="../../../static/images/s3img.jpg" alt="">
        <span class="searchName">黑色呢子大衣</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'GoodloveHeader'
  }
</script>

<style scoped>
  @import  "../../assets/goodLove.css";
  /* header */
  .header1{
    width: 100%;
    height: 0.48rem;
    line-height: .48rem;
    font-size: .17rem;
  }
  .headerLeft span{
    color:#101410;
  }
  .header1 img{
    display: inline-block;
    width: 0.21rem;
    height: 0.2rem;
    padding:.12rem;
    vertical-align: middle;
  }
  .headerLeft{
    float: left;
  }
  .headerRight{
    float: right;
  }
  .searchName{
    color:#9c9e9c;
  }
  /* search */
  .search1{
    width: 100%;
    height: 0.59rem;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-content: center;
    border-top:1px solid #d6d3d6;
    border-bottom:1px solid #d6d3d6;
    box-sizing: border-box;

    /* 假传圣旨 */
    overflow: hidden;
  }
  .seacon1{
    width: 3.3rem;
    height: 0.4rem;
    background-color: #efefef;
    line-height: .4rem;
    text-align: center;
    font-size: .13rem;
    margin:auto;
  }
  .seacon img{
    display: inline-block;
    width: 0.21rem;
    height: 0.2rem;
    padding: 0 .08rem;
    vertical-align: middle;
  }
</style>



